<template>
  <div>
    <van-nav-bar title="test View"
                 left-arrow
                 @click-left="onClickLeft" />
    <!-- <van-nav-bar title="Product View" left-arrow/> -->
    <div class="container"
         :style="{'background-color':$route.query.color}">
      <lazy-title />
      <h1 style='color:white'>{{$route.query.color}}</h1>
      <router-link :to='`/product?color=${nextColor}`'>
        Push to product "{{nextColor}}"
      </router-link>
    </div>
  </div>
</template>

<script>
import { NavBar as VanNavBar } from 'vant'
const lazyTitle = () => import(/* webpackChunkName: "lazy-title" ,webpackPrefetch: true  */ './components/Title')
// import lazyTitle from './components/Title.vue'
export default {
  name: 'Product',
  components: {
    VanNavBar,
    lazyTitle,
  },
  data() {
    return {
      nextColor: '',
      testArray: [],
    }
  },
  created() {
    for (let index = 0; index < 50000; index++) {
      this.testArray.push(new Date())
    }
    // random a link to next product
    const root = ['Teal', 'DarkBlue', 'Turquoise', 'LightSalmon']
    root.splice(root.indexOf(this.$route.query.color), 1)
    this.nextColor = root[Math.floor(Math.random() * 3)]
  },
  beforeDestroy() {
    // this.testArray.length = 0
  },
  destroyed() {
    // console.log('destroyed')
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
  },
}
</script>
<style lang="scss" scoped>
.container {
  background: orange;
}
</style>
